/**COMMONS **/
* {
  box-sizing: border-box; }

.container-max {
  display: block;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1400px; }

.container-min {
  display: block;
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  max-width: 1260px; }

.h1-title {
  font-size: 44px;
  font-weight: 900; }

.h2-title {
  font-size: 40px;
  font-weight: 900; }

.h3-title {
  font-size: 24px;
  font-weight: 900; }

p {
  color: #5C5C5C;
  font-size: 14px; }

a:hover {
  text-decoration: underline; }

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/** LAYOUT **/
/*------------------------------

    PREFOOTER

-------------------------------*/
.prefooter {
  background-color: #FFF5D8; }
  .prefooter .container {
    display: flex;
    padding: 50px 0 100px 0;
    justify-content: space-evenly; }
  .prefooter-content {
    display: flex;
    flex-direction: column; }
  .prefooter-titre {
    color: #FF9601;
    font-weight: 900;
    margin-bottom: 15px; }
  .prefooter-contact label {
    display: none; }
  .prefooter-contact form {
    display: flex; }
  .prefooter-contact input {
    height: 55px;
    border-radius: 50px;
    border: 0;
    box-shadow: 0px 10px 25px 0px rgba(0, 0, 0, 0.2); }
  .prefooter-contact .field--type-email input {
    padding: 5px 25px; }
  .prefooter-contact #edit-submit {
    min-width: 160px;
    background-color: #FF9601;
    color: white;
    font-size: 14px;
    margin-left: 10px; }
  .prefooter p {
    line-height: 1.758; }

/*------------------------------

    FOOTER

-------------------------------*/
.footer {
  background-color: #212121; }
  .footer .container {
    display: flex;
    justify-content: space-between;
    padding: 40px 0; }
  .footer p {
    color: white; }
  .footer b {
